@import 'smart-grid.less';
@import 'settings.less';

.main {
    background: url('../img/dot-grid.png');
}

/*Header*/
.main-header {
    padding-top: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main-header-logo {
    .size(3);
    .size-md(4);
    .size-xs(6);
    display: flex;
    align-items: center;

}

.main-header-descr {
    padding-left: 1rem;

    &-name {
        font-size: 1.6rem;
        .bold();
    }

    &-prof {
        font-size: 1.2rem;
    }
}

.main-header-nav {
    .size(6);
    .size-md(9);
    display: flex;
    flex-wrap: wrap;
    .justify-content(flex-end);
    .justify-content-md(flex-end);
    .justify-content-sm(space-between);
    .justify-content-xs(center);
    align-items: center;
    text-align: center;

    &-item {
        font-size: 1.4rem;
        color: #000;
        text-decoration: none;
        text-transform: uppercase;
        margin-right: 2rem;
        .bold();
        outline: none;
    }

    &-btn {
        border: 2px solid #2b2a2c;
        border-radius: 1.9rem;
        background: #fff;
        padding: 1rem 3rem;
        outline: none;

        a {
            font-size: 1.4rem;
            color: #000;
            text-decoration: none;
            text-transform: uppercase;
            .bold();
        }
    }

}

/*Бургер меню*/
.burger-menu {
    display: none;
}

.burger-menu-logo {
    text-align: left;
    color: #000000;
    text-decoration: none;
    display: flex;
    align-items: center;
    padding-top: 3rem;

    p {
        padding-left: 1rem;
        font-size: 1.4rem;
    }

    strong {
        .bold();
    }

}

/* menu button */
.menuBtn {
    height: 3rem;
    width: 3rem;
    position: absolute;
    right: 5rem;
    top: 5rem;
    z-index: 101;

    >span {
        background-color: #000;
        border-radius: 1px;
        height: 2px;
        width: 100%;
        position: absolute;
        left: 50%;
        top: 50%;
        margin: -1px 0 0 -15px;
        transition: height 100ms;

        &:after,
        &:before {
            content: '';
            background-color: #000;
            border-radius: 1px;
            height: 2px;
            width: 100%;
            position: absolute;
            left: 50%;
            margin-left: -15px;
            transition: all 200ms;
        }

        &:after {
            top: -7px;
        }

        &:before {
            bottom: -7px;
        }
    }

    &.act {
        >span {
            height: 0;

            &:after,
            &:before {
                background-color: #fff;
                top: 1px;
            }

            &:after {
                transform: rotate(45deg);
            }

            &:before {
                transform: rotate(-45deg);
            }
        }
    }
}

/* main menu block */
.mainMenu {
    background-color: #000;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 100;
    height: 100%;
    width: 100%;
    display: table;
    text-align: center;
    opacity: 0;
    transition: all 500ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
    transform: scale(0);

    &.act {
        opacity: 0.9;
        transform: scale(1);

        ul li {
            opacity: 1;
            transform: translateX(0);
        }
    }

    ul {
        display: table-cell;
        vertical-align: middle;
    }

    li {
        padding: 8px 0;
        transition: all 400ms 510ms;
        opacity: 0;

        &:nth-child(odd) {
            transform: translateX(30%);
        }

        &:nth-child(even) {
            transform: translateX(-30%);
        }

        &:last-child {
            transform: none;
        }
    }

    a {
        color: #fff;
        display: inline-block;
        font-size: 3rem;
        text-decoration: none;

        &.suBtn {
            color: #fff;
        }

        &.suBtn:hover {
            color: #000;
            transition: 0.5s;
        }
    }

}

/* sign up button */
.suBtn {
    border: 2px solid #ffffff;
    border-radius: 1.9rem;
    background: transparent;
    padding: 1rem 3rem;
    outline: none;
    font-size: 2rem;

}

.suBtn:hover {
    background: #ffffff;
    color: #000;
    transition: 0.5s;
}

/*Приветствие*/
.greatings-titles {
    display: flex;
    flex-wrap: wrap;
    .justify-content(space-between);
    .justify-content-sm(center);
    padding-top: 10rem;
    font-size: 5rem;
}

.greatings-title {
    .size(6);
    .size-sm(12);
    .bold();
}

.greatings-descr {
    .size(6);
    .size-sm(12);
    .bold();
    text-align: right;
}

/*К портфолио*/
.down-link {
    padding-top: 10rem;
    padding-bottom: 6rem;
    text-align: center;
    font-size: 3rem;
    .bold();
}

.to-portfolio {
    display: block;
    padding-top: 1rem;
    opacity: 0.5;
    cursor: pointer;

    a {
        text-decoration: none;
        color: #000;
    }
}

/*Карточки портфолио*/

.portfolio {
    background: url('../img/so-white.png');
}

.portfolio-title {
    text-align: center;
    font-size: 3rem;
    .bold();
    padding-top: 7rem;
}

.portfolio-cards {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 10rem 0 5rem 0;
}

.portfolio-card {
    .size(4);
    .size-md(6);
    .size-sm(8);
    .size-xs(11);

    border-radius: 1rem;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    margin-bottom: 5rem;

    &-img img {
        width: 100%;
        border-radius: 10px 10px 0 0;
    }

    &-descr {
        padding: 1rem 2rem;
        background: #fff;
        border-radius: 0 0 10px 10px;
    }

    &-type {
        font-size: 1.2rem;
        color: #2b2a2c;
    }

    &-footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 1rem;
    }

    &-name {
        font-size: 1.6rem;
        .bold();
    }

    &-view a {
        text-decoration: none;
        font-size: 1.5rem;
        .bold();
        color: #000;
    }

    &-view a:hover {
        opacity: 0.5;
    }

}

/*Связь со мной*/

.feedback {
    background: url('../img/email-pattern.png');
}

.feedback-links {
    padding-top: 5rem;
    padding-bottom: 9rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    text-align: center;
}

.feedback-work-social,
.feedback-social {
    .size(4);
    .size-sm(12);
}


.feedback-title {
    text-align: center;
    font-size: 3rem;
    .bold();
    padding-top: 7rem;
}

.feedback-subtitle {
    text-align: center;
    font-size: 2rem;
    padding-top: 2rem;
}

.feedback-social-telegram {
    font-size: 1.6rem;
    padding-bottom: 2.5rem;


    span {
        .bold();

        a {
            font-weight: normal;
            text-decoration: none;
            color: #000;
            text-decoration: underline;
        }
    }


}

.feedback-social-instagram {
    font-size: 1.6rem;

    span {
        .bold();

        a {
            font-weight: normal;
            text-decoration: none;
            color: #000;
        }
    }
}

.feedback-work-email {
    font-size: 1.6rem;
    padding-bottom: 2.5rem;

    strong {
        .bold();
    }
}

.feedback-work-linkedin {
    font-size: 1.6rem;

    span {
        .bold();

        a {
            font-weight: normal;
            text-decoration: none;
            color: #000;
            text-decoration: underline;
        }
    }
}

/*Футер*/
.footer {
    background: #2b2a2c;
}

.footer-items {
    display: flex;
    .justify-content(space-between);
    .justify-content-xs(center);
    flex-wrap: wrap;
    align-items: center;
    padding: 1rem 0;
}

.footer-left-side-item {
    .size(6);
    .size-xs(12);

    a {
        text-decoration: none;
        color: #8d8d8e;
        font-size: 1.4rem;
        padding-right: 2rem;

        &:hover {
            color: #fff;
        }
    }
}

.footer-right-side {
    .size(6);
    .size-xs(12);
    text-align: right;

    &-btn {
        border: 1px solid #8d8d8e;
        border-radius: 1.9rem;
        background: transparent;
        padding: 1rem 3rem;
        outline: none;
    }

    &-btn:hover {
        border: 1px solid #fff;
        cursor: pointer;

        a {
            color: #fff;
        }
    }

    a {
        font-size: 1.4rem;
        color: #8d8d8e;
        text-decoration: none;
        text-transform: uppercase;
        .bold();
    }

}



@media screen and (max-width:992px) {
    .main-header-nav-btn {
        margin-left: 2rem;
    }

    .greatings-titles {
        font-size: 3.5rem;
    }

    .portfolio-card-name {
        font-size: 1.5rem;
    }

    .portfolio-card-view a {
        font-size: 1.3rem;
    }
}

@media screen and (max-width:720px) {
    .main-header {
        display: none;
    }

    .burger-menu {
        display: block;
    }

    .greatings-titles {
        font-size: 3rem;
    }

    .greatings-title {
        text-align: center;
        padding-bottom: 3rem;
    }

    .greatings-descr {
        text-align: center;
    }

    .feedback-social {
        padding-bottom: 2.5rem;
    }
}

@media screen and (max-width:576px) {
    .menuBtn {
        right: 3rem;
    }

    .burger-menu-logo {
        margin-left: -1.5rem;
    }

    .down-link {
        font-size: 2rem;
    }

    .footer-left-side {
        text-align: center;
    }

    .footer-right-side {
        text-align: center;
        margin-top: 2rem;
    }

    .footer-left-side-item a {
        padding: 0 2rem;
    }
}